@import "shared/assets/help-styles/variables.scss";

.header {
	min-height: 60px;
	font-family: $font-3;
	display: flex;
	align-items: center;
	background-color: #f9f9f9;
	border-bottom: solid 1px #b5b5b7;
}
.back {
	color: #007aff;
	position: absolute;
	text-decoration: none;
	left: 7px;
}
.container {
	display: flex;
	width: 100%;
	justify-content: center;
	position: absolute;
}
