@import "shared/assets/help-styles/variables.scss";

.counter {
	color: #424950;
	font-size: 16px;
	line-height: 18px;
	padding: 9px 0;
	border-radius: 4px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.counterBtn {
	width: 36px;
	height: 36px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #f2f3f5;
	border-radius: 4px;
	color: #424950;
	font-size: 30px;
}
.number {
	font-family: $font-1;
	margin: 0 30px;
	font-size: 15px;
}
